<template>
  <v-container class="fill-height" fluid>
    <v-row>
      <v-col :cols="12">
        <v-container v-if="!logged">
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <v-form>
                <v-text-field
                  v-model="name"
                  :counter="10"
                  label="Name"
                  required
                ></v-text-field>
                <v-text-field
                  v-model="room"
                  :counter="10"
                  label="Room"
                  required
                ></v-text-field>
                <v-btn color="green" class="mr-4" @click="createUser">
                  Go
                </v-btn>
              </v-form>
            </v-col>
          </v-row>
        </v-container>
        <v-container v-else>
          <v-row class="px-2">
            <v-col>
              <p v-if="!users.length">Please wait while the website is trying to access to the server</p>
              <p>Current users: {{ users.length }} users</p>
              <v-card-text v-for="user in users" :key="user.id">
                <div>Name</div>
                <p class="text-h4 text--primary">
                  {{ user.name }}
                  <span v-if="user.id == socket.id" class="text-center">
                    (You)
                    <v-btn align="right" @click="exitRoom"> Exit </v-btn>
                  </span>
                </p>
              </v-card-text>
              <div class="d-flex">
              <v-text-field
                v-model="numberValue"
                label="Loss chance (0 to 1.0)"
                outlined
                type="number"
              />
              <v-btn @click="setLoss" x-large> Set </v-btn>
              </div>
            </v-col>
          </v-row>
          <v-row class="px-2">
            <v-col>
              <Editor
                ref="editor"
                v-model="code"
                @input="socketUpdateText"
                @newCursor="socketUpdateCursor"
              />
            </v-col>
          </v-row>
        </v-container>
        <v-snackbar v-model="snackbar" :timeout="timeout">
          {{ notiContent }}
        </v-snackbar>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
const TextController = require("../util/textController");
const XMLHttpRequest = require('xhr2');
import io from "socket.io-client";
import Editor from "../components/Editor";
const ENDPOINT = "https://diffsyncalter-server.herokuapp.com/";
// const ENDPOINT = "http://localhost:3017";
export default {
  data() {
    return {
      logged: false,
      value: {
        type: String,
        required: false,
      },
      connected: false,
      socket: {},
      intel: 1,
      name: "",
      room: "",
      snackbar: false,
      timeout: 3000,
      notiContent: "",
      users: [],
      code: "",
      clp: "",
      tc: undefined,
      resetPass: 0,
      resetTextPass: 0,
      numberValue: 0,
      lossChance: 0,
    };
  },
  components: {
    Editor,
  },
  mounted() {},
  methods: {
    setLoss() {
      this.numberValue = Math.max(this.numberValue, 0.0);
      this.numberValue = Math.min(this.numberValue, 1.0);
      this.lossChance = this.numberValue;
    },
    createUser() {
      if (this.name === "" || this.room === "") {
        return;
      }
      this.initializeSocket();
      this.logged = true;
      console.log(this.name, this.room);
      let name = this.name;
      let room = this.room;
      this.socket.emit("join", {
        name,
        room,
      });
    },
    exitRoom() {
      this.logged = false;
      this.code = "";
      this.socket.close();
    },
    initializeSocket() {
      this.socket = io(ENDPOINT, {
        transports: ["websocket", "polling", "flashsocket"],
        enabledTransports: ['ws', 'wss'],
      });
      this.socket.on("notification", (data) => {
        this.notiContent = data.text;
        this.snackbar = true;
      });
      this.socket.on("init", (data) => {
        this.tc = new TextController(data);
        this.code = this.tc.code
      }); 
      this.socket.on("roomData", (data) => {
        this.users = data.users;
      });
      this.socket.on("text", (data) => {
        this.tc.receivePackage(data);
        this.code = this.tc.code;
        this.resetPass ++;
        this.resetTextPass ++;
      });
      setInterval(() => {
        if (this.logged) {
          this.socket.emit("sendText", this.tc.pushPackage());
        }
      }, 1000);
    },
    socketUpdateCursor(data) {
      if (data.line == 0 && data.ch == 0 && this.resetPass) {
        this.resetPass--;
        this.$refs.editor.modifyCursor(this.tc.setCursor());
      } else {
        this.tc.updateCursor(data.line, data.ch);
      }
    },
    socketUpdateText(data) {
      if (this.tc.code === data) { // Avoid echoing
        return;
      }
      this.tc.code = data;
      if (Math.random() >= this.lossChance) {
        this.socket.emit("sendText", this.tc.pushPackage());
      }
    },
  },
  created() {
    // wake the heroku server
    var xhr = new XMLHttpRequest();
    xhr.open('GET', ENDPOINT, true);
    xhr.send();
  }
};
</script>

<style></style>
