const DiffPatchMatch = require("diff-match-patch");
const dmp = new DiffPatchMatch();

class TextController {
  constructor(code = "") {
    this.code = code;
    this.shadow = code;
    this.cursorPos = 0;
    this.localVersion = 0;
    this.remoteVersion = 0;
    this.editStack = [];
  }
  makePatch(oldCode, newCode) {
    return dmp.patch_make(oldCode, newCode);
  }
  addLocalChange() {
    let patch = this.makePatch(this.shadow, this.code);
    if (patch.length) {
      this.editStack.push({
        patch,
        version: this.localVersion
      });
      this.applyPatchToShadow(patch);
      this.localVersion ++;
    }
  }
  receivePackage(data) {
    let nextRemoteVersion = this.remoteVersion;
    for (let i = 0; i < data.patches.length; i ++) {
      if (data.patches[i].version < this.remoteVersion) {
        continue;
      }
      this.applyPatchToLocal(data.patches[i].patch);
      this.applyPatchToShadow(data.patches[i].patch);
      nextRemoteVersion = Math.max(nextRemoteVersion, data.patches[i].version + 1);
    }
    this.remoteVersion = nextRemoteVersion;
    while (this.editStack.length && this.editStack[0].version < data.latestVersion) {
      this.editStack.shift();
    }
    this.addLocalChange();
  }
  applyPatchToLocal(patches) {
    let result = dmp.patch_apply(patches, this.code);
    this.code = result[0];
    // this.shadow = this.code;
    let patchApplied = 0;
    for (let i = 0; i < result[1].length; i++) {
      if (result[1][i]) {
        patchApplied++;
      }
    }
    let iter = 0;
    let delta = 0;
    for (let i = 0; i < patches.length; i++) {
      if (result[1][i]) {
        if (patches[i].start1 !== patches[i].start2) {
          console.log("Error at patch ", i);
        }
        if (patches[i].start1 + patches[i].length1 <= this.cursorPos)
          delta += patches[i].length2 - patches[i].length1;
        else if (patches[i].start1 >= this.cursorPos) break;
        else {
          iter = patches[i].start1;
          for (let j = 0; j < patches[i].diffs.length; j++) {
            if (patches[i].diffs[j][0] == 0) {
              iter += patches[i].diffs[j][1].length;
            } else if (patches[i].diffs[j][0] == 1) {
              if (iter <= this.cursorPos) {
                delta += patches[i].diffs[j][1].length;
              }
            } else {
              iter += patches[i].diffs[j][1].length;
              if (iter <= this.cursorPos) {
                delta -= patches[i].diffs[j][1].length;
              }
            }
          }
        }
      }
    }
    this.cursorPos += delta;
    if (this.cursorPos > this.code.length) 
      this.cursorPos = this.code.length;
    return patchApplied;
  }
  applyPatchToShadow(patches) {
    let result = dmp.patch_apply(patches, this.shadow);
    this.shadow = result[0];
  }
  pushPackage() {
    this.addLocalChange();
    return {
      patches: this.editStack,
      latestVersion: this.remoteVersion
    };
  }
  updateCursor(line, ch) {
    let iter = 0;
    let lineCount = 0;
    while (lineCount < line) {
      if (this.code[iter] == "\n") {
        lineCount++;
      }
      iter++;
    }
    iter += ch;
    this.cursorPos = iter;
  }
  setCursor() {
    let lastEndlineFound = -1;
    let lineCount = 0;
    for (let i = this.cursorPos - 1; i >= 0; i--) {
      if (this.code[i] === "\n") {
        lineCount++;
        if (lastEndlineFound == -1) {
          lastEndlineFound = i;
        }
      }
    }
    return {
      line: lineCount,
      ch: this.cursorPos - 1 - lastEndlineFound,
    };
  }
}

module.exports = TextController;
